<template>
  <div class="login-style" :style="`padding-top:${top}`">
    <div class="back" @click="goHome">
      <i class="iconfont icon-guanbi"></i>
    </div>
    <div class="title">
      <h1>{{ title }}</h1>
      <p v-show="desc">{{ desc }}</p>
    </div>
    <div class="inp-box">
      <input v-if="showNickname" type="text" v-model="nickname" placeholder="用户名">
      <input type="text" v-model="phone" placeholder="手机号">
      <div style="display: flex;justify-content: space-between" v-if="showNickname">
        <input type="text" v-model="Verification" placeholder="验证码" style="width: 70%">
        <span v-if="yanz"  style="padding-top:10px;color:#63b9ff;display: inline-block;">{{time}}s</span>
        <span v-else @click="getVerification"  style="padding-top:10px;color:#63b9ff;display: inline-block;">发送验证</span>
      </div>
      <input type="password" v-model="password" placeholder="密码">
      <router-link v-show="showForget" to="/forget">Forget Your Password</router-link>
    </div>
    <div class="btn">
      <a href="javascript:;" @click="handleSubmit" class="login">{{ btnData.redText }}</a>
      <div class="line">
        <span></span>
        <span>or</span>
        <span></span>
      </div>
      <router-link :to="btnData.blueLink" class="sign">{{ btnData.blueText }}</router-link>
    </div>
  </div>
</template>
<script>
import { Toast  } from 'vant';
import { calRealLength } from '@/assets/js/common';
import ajax from '../../api/ajax'
import { POST_REGISTER,POST_YANZ } from '@/api'
export default {
  data () {
    return {
      time:60,
      yanz:false,
      nickname: '',
      phone: '',
      password: '',
      Verification:'',
      zq:'',
      newPhone:''
    }
  },
  props: {
    showNickname: {
      type: Boolean,
      default: false
    },
    top: {
      type: String,
      default: '0px'
    },
    title: {
      type: String,
      default: ''
    },
    desc: {
      type: String,
      default: ''
    },
    showForget: {
      type: String,
      default: ''
    },
    btnData: {
      type: Object,
      default: {}
    }
  },
  methods: {
    getVerification(){
      if (!this.phone) {
        return Toast('请输入手机号码')
      }
      if (!/^1[3456789]\d{9}$/.test(this.phone)) {
        return Toast('请输入正确的手机号码')
      }
      this.yanz=true;
      var that=this;
      var timer=setInterval(()=>{
        this.time=that.time-1;
        if(that.time<=1){
          this.time=60;
          clearInterval(timer);
          that.yanz=false;
        }
      },1000);
      POST_YANZ(this.phone).then(res=>{
        if(res.status==0){
          this.zq=res.code;
          this.newPhone=this.phone;
          return Toast('验证码发送成功！')
        }else{
          return Toast('发送失败')
        }
      });
    },
    handleSubmit () {
      if (this.showNickname) {
        if (!this.nickname) {
          return Toast('请输入用户名')
        }
        let pattern = new RegExp("[`~^*()|{}\\[\\].<>/~#￥……*（）——|{}【】‘；：”“']")
        if (pattern.test(this.nickname)) {
          return Toast('存在特殊字符')
        }
        let reallength = calRealLength(this.nickname)
        if (reallength < 4 || reallength > 7) {
          return Toast('昵称长度必须在4-7个字符之间')
        }
      }
      if (!this.phone) {
        return Toast('请输入手机号码')
      }
      if (!/^1[3456789]\d{9}$/.test(this.phone)) {
        return Toast('请输入正确的手机号码')
      }
      if(this.showNickname){
        if(!/[0-9]{6,6}$/.test(this.Verification)){
          return Toast('请输入验证码')
        }
      }
      if (!this.password) {
        return Toast('请输入密码')
      }
      if (this.password.length < 6 || this.password.length > 12) {
        return Toast('请输入6-12位密码')
      }
      //判断是否为注册是的话就验证 验证码是否正确
      if(this.showNickname){
        if(this.newPhone!=this.phone){
          return Toast("手机号或验证码错误")
        }
        if(this.Verification==this.zq){
          this.$emit('submitClick', this.$data)
        }else{
          return Toast('请输入正确的验证码')
        }
        //如果不是注册就直接登录
      }else{
        this.$emit('submitClick', this.$data)
      }
    },
    goHome () {
      this.$router.push({
        path: '/home'
      })
    }
  }
};
</script>
<style lang="less" scoped>
.login-style {
  padding-left: 40px;
  padding-right: 40px;
  color: #fff;
  .back{
    position: absolute;
    top: 12px;
    left: 12px;
    i{
      font-size: 24px;
      color: #fff;
    }
  }
  .title {
    margin-bottom: 20px;
    h1 {
      font-size: 20px;
      font-weight: 600;
    }
    p {
      padding-top: 10px;
    }
  }
  .inp-box {
    margin-bottom: 20px;
    input {
      border: 1px solid rgba(255, 255, 255, 0.2);
      outline: none;
      background: transparent;
      width: 100%;
      height: 34px;
      border-radius: 20px;
      padding-left: 20px;
      margin-bottom: 14px;
      color: #fff;
    }
    input::-webkit-input-placeholder {
      /* WebKit browsers */
      font-size: 14px;
      color:#fff;
    }
    input::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      font-size: 14px;
      color:#fff;
    }
    input:-ms-input-placeholder {
      /* Internet Explorer 10+ */
      font-size: 14px;
      color:#fff;
    }
    a{
      color: #fff;
      text-decoration: underline;
      padding-left: 16px;
      font-size: 12px;
    }
  }
  .btn{
    a{
      display: block;
      width: 100%;
      height: 32px;
      text-align: center;
      line-height: 32px;
      border-radius: 20px;
      color: #fff;
    }
    .login{
      background: #E2B96F;
    }
    .sign{
      background: #63b9ff;
    }
    .line{
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 6px 0;
      span:first-child,span:last-child{
        width: 80px;
        height: 1px;
        background: rgba(255, 255, 255, 0.4);
      }
      span:nth-child(2){
        margin: 0 14px;
      }
    }
  }
}
</style>



